/* src/App.css */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Arial', sans-serif;
  color: #333;
}

.app-container {
  text-align: center;
}

.navbar {
  display: flex;
  justify-content: space-between;
  padding: 1.5rem 3rem;
  background-color: #333;
}

.logo {
  color: #fff;
  font-size: 1.5rem;
}

.nav-links {
  list-style: none;
  display: flex;
}

.nav-links li {
  margin-left: 2rem;
}

.nav-links a {
  color: #fff;
  text-decoration: none;
  font-size: 1.1rem;
}

.hero-section {
  height: 80vh;
  background: linear-gradient(to right, #141E30, #243B55);
  color: white;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 0 2rem;
}

.hero-section h1 {
  font-size: 3rem;
  margin-bottom: 1.5rem;
}

.hero-section p {
  font-size: 1.2rem;
  margin-bottom: 2rem;
}

.cta-button {
  padding: 1rem 2rem;
  background-color: #007bff;
  color: white;
  text-decoration: none;
  border-radius: 25px;
  font-size: 1rem;
}

.features-section {
  padding: 5rem 0;
  display: flex;
  justify-content: space-around;
  background-color: #f4f4f4;
}

.feature {
  width: 25%;
  text-align: center;
}

.feature h2 {
  font-size: 1.5rem;
  margin-bottom: 1rem;
}

.footer {
  background-color: #333;
  color: white;
  padding: 1rem 0;
  margin-top: 2rem;
}
